import http from '../../common/http.js';
export default {
	data() {
		return {
			tel: '', // 手机号码
			content: '',
			title: '投诉建议',
			winHeight: 0
		}
	},
	methods: {
		// 提交投诉
		submitComplaint() {
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			const url = apiPrefix + "?mod=lyb&ac=lyb_add";
			http.request(url, {
				phone: this.tel,
				content: this.content,
				auth:auth,
				filterData:true,
			}).then(resp=>{
				if(resp.status == 1) {
					//console.log('resp', resp);
					uni.showModal({
						title: '提示',
						content: '感谢你提交的投诉和建议',
						showCancel: false,
						cancelText: '',
						confirmText: '确定',
						success: res => {
							uni.navigateBack({
								url: '/pages/user/index'
							});
						},
						fail: () => {},
						complete: () => {
							
						}
					});
				}
			});
		},
		onSubmit() {
			let isValid = this.validateFunc();
			if(isValid) {
				this.submitComplaint();
			}
		},
		// 校验方法
		validateFunc() {
			let isRequired = this.isRequired();
			if(!isRequired) {
				uni.showToast({
					title: '手机号码和建议内容必填!',
					icon:"none",
					duration: 2000
				});
				return;
			}
			let isValid = this.isPoneAvailable();
			if(!isValid) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon:"none",
					duration: 2000
				});
				return;
			}
			let isContentValid = this.isContentValid();
			if (!isContentValid) {
				uni.showToast({
					title: '建议内容不超过300个字',
					icon:"none",
					duration: 2000
				});
				return;
			}
			return true;
		},
		isContentValid() {
			let s = this.content.length;
			if(s>300) {
				return false;
			}
			return true;
		},
		isRequired() {
			if (!this.tel || !this.content) {
				return false;
			}
			return true;
		},
		isPoneAvailable() {
			let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
			if (!myreg.test(this.tel)) {
				return false;
			} else {
				return true;
			}
		}
	},
	onLoad() {
		uni.showShareMenu({
		    withShareTicket: true
		});
		let winHeight = uni.getSystemInfoSync().windowHeight;
		this.winHeight = winHeight+'px';
	},
	onShareAppMessage() {
		let pages = getCurrentPages(); //获取加载的页面
		let currentPage = pages[pages.length-1]; //获取当前页面的对象
		let url = currentPage.route; //当前页面url
		return {
			title: '投诉建议',
			path: url,
			success: function() {
			},
			fail: function() {
			}
		};
	}
}
